olor-danger, 1);\n}\n","@import \"../../lib/typography\";\n\n\n.mmf-input {\n    @include font-regular();\n    color: inherit;\n    font-size: inherit;\n    font-family: inherit;\n}\n",".mmf-label {\n    color: inherit;\n    font-family: inherit;\n    font-size: inherit;\n    user-select: none;\n    color: map-get($color-basic, 7);\n}\n","@import \"../../lib/colors\";\n@import \"../../lib/typography\";\n\n\n.mmf-select__wrapper {\n    &:after {\n        font-size: 1.25em;\n        color: map-get($color-basic, 7);\n    }\n}\n\n.mmf-select {\n    font-family: inherit;\n    font-size: inherit;\n\n    option {\n        font-size: inherit;\n        color: map-get($color-basic, 7);\n    }\n}\n","@import \"../../lib/colors\";\n\n\n.mmf-textarea {\n    font-size: inherit;\n    font-family: inherit;\n}\n\n.mmf-form.hasError .mmf-textarea {\n    border-color: map-get($color-danger, 1);\n}\n","@import \"../../lib/typography\";\n\n\n.the-ux.mmf-form--textarea {\n\n    textarea {\n        @include font-regular();\n        color: inherit;\n        font-size: inherit;\n        line-height: 1.4em;\n    }\n}\n","@import \"../components/button/typography\";\n@import \"../components/errors/typography\";\n@import \"../components/input/typography\";\n@import \"../components/label/typography\";\n@import \"../components/select/typography\";\n@import \"../components/textarea/typography\";\n@import \"../components/textareaform/typography\";\n@import \"../lib/units\";\n@import \"../lib/colors\";\n\n\n.mmf-form__errors {\n    font-size: $mmf-text-size--errors;\n    color: map-get($color-danger, 1);\n}\n\n.mmf-meta {\n    opacity: map-get($opacity, 1);\n    font-style: italic;\n    font-size: $mmf-text-size--meta;\n}\n\n.mmf-form.is-disabled {\n    .mmf-meta,\n    .mmf-form__errors,\n    span.mmf-label { // be more specific to overwrite form+label combination\n        opacity: map-get($opacity, 2);\n    }\n}\n","@import \"../lib/with-solid-theme\";\n\n\n.the-solid {\n    @include with-solid-theme();\n}\n","@import \"../lib/units\";\n\n@mixin solid-font-style() {\n    font-size: $mmf-font-normal;\n    line-height: $mmf-line-height;\n    letter-spacing: $mmf-letter-spacing;\n}\n","@import \"../../lib/reset\";\n@import \"../../lib/colors\";\n@import \"../../lib/units\";\n@import \"../../lib/fonts\";\n\n\n@mixin button-solid() {\n\n    &.mmf-button {\n        @include reset-button();\n        @include solid-font-style();\n        background-color: map-get($color-solid, 6);\n        border: $mmf-input-border--width $mmf-style--border transparent;\n        border-radius: $mmf-radius;\n        color: map-get($color-solid, 8);\n        padding: 0 $mmf-unit;\n\n        &[disabled] {\n            color: rgba(map-get($color-solid, 8), map-get($opacity, 2));\n        }\n\n        // clickable\n        &.is-enabled {\n            &:hover,\n            &:focus {\n                background-color: map-get($color-solid, 9);\n                color: map-get($color-solid, 0);\n            }\n\n            &:focus {\n                @include elevation(1);\n            }\n\n            // activated until deactivated via being clicked again\n            &.activated {\n                background-color: map-get($color-solid, 5);\n                color: map-get($color-solid, 0);\n            }\n        }\n    }\n\n    &.mmf-button--raised {\n        border: $mmf-input-border--width solid map-get($color-solid, 5);\n        border-radius: $mmf-radius;\n        background-color:map-get($color-solid, 5);\n        color: map-get($color-solid, 0);\n        \n        &[disabled] {\n            background-color: rgba(map-get($color-solid, 5), map-get($opacity, 2));\n            border-color: rgba(map-get($color-solid, 5), map-get($opacity, 2));\n            color: rgba(map-get($color-solid, 0), map-get($opacity, 2));\n        }\n\n        &.is-enabled {\n            &:hover {\n                border: $mmf-input-border--width $mmf-style--border map-get($color-basic, 5);\n                border-radius: $mmf-radius;\n                background-color:map-get($color-basic, 5);\n            }\n\n            &:focus {\n                @include elevation(1);\n                opacity: map-get($opacity, 0);\n            }\n        }\n    }\n}\n","@import \"../../lib/units\";\n\n\n@mixin buttonform-solid(){\n    &.mmf-form--button {\n        \n        .mmf-label {\n            @include solid-font-style();\n            display: block;\n            padding-bottom: $mmf-unit;\n            color: map-get($color-basic, 0);\n        }\n    }\n}\n","@import \"../../lib/colors\";\n@import \"../../lib/units\";\n\n\n@mixin checkbox-solid() {\n    &.mmf-checkbox {\n        outline: 0;\n        border: $mmf-input-border--width $mmf-style--border map-get($color-basic, 3);\n        border-radius: $mmf-radius;\n        transition: border-color $mmf-transition;\n        width: 3 * $mmf-unit;\n        height: 3 * $mmf-unit;\n\n        &::after {\n          content: \"done\";\n          font-size: $mmf-font-big;\n          transform: translate(10%, 10%); // center \n        }\n\n        &:checked{\n            background-color: map-get($color-solid , 2);\n            border-color: map-get($color-solid, 0);\n            &::after {\n                color: white;   \n            }\n\n            &:not([disabled]):hover {\n                background-color: map-get($color-solid, 3);\n    \n            }\n        }\n\n        &:not(:checked):after{\n            opacity: 0;\n        }\n\n        &:not([disabled]):focus {\n            border-color: map-get($color-solid, 0);\n            @include elevation(1);\n        }\n    \n        &:not([disabled]):hover {\n            border-color: map-get($color-solid, 0);\n\n        }\n    \n        &[disabled] {\n            border-color: map-get($color-basic, 5);\n        }\n    }\n}\n","@import \"../../lib/colors\";\n@import \"../../lib/units\";\n@import \"../../lib/fonts\";\n\n\n@mixin checkboxform-solid(){\n    \n\n        label {\n            @include solid-font-style();\n        }\n\n        .order--label-first .mmf-label {\n            padding-right: $mmf-unit;\n        }\n\n        .order--label-last .mmf-label {\n            padding-left: $mmf-unit;\n        }\n    }\n","@import \"./colors\";\n@import \"./typography\";\n@import \"./units\";\n\n\n@mixin with-bottom-border() {\n    // border-bottom: $mmf-input-border--width $mmf-style--border transparent;\n    border-bottom: $mmf-input-border--width $mmf-style--border rgba(map-get($color-basic, 7), map-get($opacity, 1));\n    &:not([disabled]):focus {\n        border-bottom: $mmf-input-border--width $mmf-style--border rgba(map-get($color-material, 0), map-get($opacity, 1));\n    }\n    &:not([disabled]):hover {\n        border-bottom: $mmf-input-border--width $mmf-style--border rgba(map-get($color-material, 0), map-get($opacity, 1));\n    }\n}\n\n@mixin with-input() {\n    color: rgba(map-get($color-basic, 7), map-get($opacity, 0));\n    border: none;\n    border-radius: 0;\n    width: 100%;\n    font-size: inherit;\n    box-sizing: border-box;\n    background: transparent;\n    transform: translateZ(0);\n\n    outline: none;\n    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n\n    transition: border-color $mmf-transition;\n\n    &:not([disabled]):focus {\n        border-color: rgba(map-get($color-material, 0), 1);\n    }\n\n    &:not([disabled]):hover {\n        border-color: map-get($color-material, 0);\n    }\n\n    &[disabled] {\n        color: map-get($color-basic, 7); // on deactivated state, set color to match labels\n        opacity: map-get($opacity, 2);\n    }\n}\n\n\n@mixin with-input-solid($is-form-element: true) {\n    color: map-get($color-basic, 0);\n    font-size: $mmf-font-normal;\n    line-height: $mmf-line-height;\n    letter-spacing: $mmf-letter-spacing;\n    padding: $mmf-unit;\n    border: $mmf-input-border--width $mmf-style--border map-get($color-basic, 4);\n    border-radius: $mmf-radius;\n    outline: 0;\n    transition: border-color $mmf-transition;\n\n    .has-error & {\n        border-color: map-get($color-danger, 1);\n        &:not([disabled]):hover,\n        &:not([disabled]):focus{\n            border-color: map-get($color-danger, 2);\n        }\n    }\n\n    @if $is-form-element {\n        &:not([disabled]):focus {\n            border-color: map-get($color-solid, 0);\n            @include elevation(1);\n        }\n\n        &:not([disabled]):hover {\n            border-color: map-get($color-solid, 0);\n        }\n\n        &[disabled] {\n            color: rgba(map-get($color-basic, 7), map-get($opacity, 2));\n            border-color: map-get($color-basic, 5);\n        }\n\n        &::placeholder {\n            color: map-get($color-basic, 3);\n            font-style: italic;\n        }\n    }\n}\n","@import \"../../lib/units\";\n@import \"../../lib/colors\";\n@import \"../../lib/elevation\";\n\n\n@mixin list-solid() {\n    &.mmf-list {\n        .is-value {\n            font-style: italic;\n            &:before { content: \"„\"; }\n            &:after { content: \"“\"; }\n        }\n        .is-selected {\n            color: map-get($color-solid, 0);\n            background-color: map-get($color-basic, 5);\n        }\n    }\n}\n","@import \"../../lib/units\";\n@import \"../../lib/colors\";\n@import \"../../lib/elevation\";\n\n\n@mixin popover-solid() {\n    &.mmf-popover {\n        border-radius: $mmf-radius;\n        background-color: map-get($color-solid, 6);\n        @include elevation(4);\n    }\n}\n","@import \"../../lib/colors\";\n@import \"../../lib/units\";\n@import \"../../lib/fonts\";\n\n\n@mixin radiobuttons-solid(){\n    .mmf-radio-btn-container {\n        height: 0.75 * $mmf-block;\n        display: contents;\n    }\n    \n    .mmf-radio-btn {\n        @include solid-font-style();\n        border: $mmf-input-border--width $mmf-style--border transparent;\n        border-radius: $mmf-radius;\n        background: transparent;\n        display: inline-flex;\n        flex-direction: row;\n        align-items: center;\n        margin: $mmf-unit/2 $mmf-unit / 2;\n        \n        &.is-disabled {\n            border: $mmf-input-border--width $mmf-style--border transparent;\n        }\n    \n        &:focus {\n            outline: none;\n            background-color:map-get($color-basic, 5);\n            color: map-get($color-solid, 0);\n            @include elevation(1);\n        }\n    \n        &:not([disabled]):hover {\n            border: $mmf-input-border--width $mmf-style--border map-get($color-basic, 5);\n            border-radius: $mmf-radius;\n            background-color:map-get($color-basic, 5);\n            color: map-get($color-solid, 0);\n        }\n    \n        &.selected {\n            border: $mmf-input-border--width $mmf-style--border map-get($color-solid, 5);\n            background-color: map-get($color-solid, 5);\n            border-radius: $mmf-radius;\n            color: map-get($color-solid, 0);\n            \n            &.is-disabled {\n                background-color: rgba(map-get($color-solid, 5), map-get($opacity, 2));\n                color: rgba(map-get($color-solid, 0), map-get($opacity, 2));\n                border-color: transparent;\n            }            \n        }\n    \n        &:not([disabled]).selected:hover {\n            opacity: map-get($opacity, 0);\n            border: $mmf-input-border--width solid map-get($color-solid, 5);\n            background-color:map-get($color-solid, 5);\n        }\n\n        &:not([disabled]).selected:focus {\n            opacity: map-get($opacity, 0);\n            border: $mmf-input-border--width solid map-get($color-solid, 5);\n            background-color:map-get($color-solid, 5);\n        }\n    }\n    \n}\n","@import \"../../lib/units\";\n@import \"../../lib/reset\";\n@import \"../../lib/input\";\n@import \"../../lib/fonts\";\n\n\n@mixin select-solid() {\n    &.mmf-select__wrapper {\n        @include with-input-solid(false);\n        border: none;\n        padding: 0; // reset default input padding from mixin\n        background-color: map-get($color-solid, 6);\n\n        &.is-enabled {\n            &:hover {\n                border-color: map-get($color-solid, 0);\n            }\n\n            &.has-focus {\n                border-color: map-get($color-solid, 0);\n                @include elevation(1);\n            }\n\n            &:hover,\n            &.has-focus {\n                &:after {\n                    background-color: map-get($color-solid, 5);\n                    color: map-get($color-solid, 0);\n                }\n            }\n        }\n\n        &.is-disabled {\n            color: rgba(map-get($color-solid, 7), map-get($opacity, 2));\n            border-color: map-get($color-basic, 5);\n        }\n\n        &:after {\n            content: \"arrow_drop_down\";\n            font-size: 1.8em;\n            color: map-get($color-basic, 0);\n            margin: $mmf-unit/2 0;\n        }\n\n        .mmf-select {\n            @include reset-select();\n            @include solid-font-style();\n            font-size: inherit;\n            outline: none;\n            padding: $mmf-unit 5*$mmf-unit $mmf-unit $mmf-unit;\n            color: map-get($color-basic, 0);\n            border: $mmf-input-border--width $mmf-style--border map-get($color-basic, 4);\n            border-radius: $mmf-radius;\n        }\n        \n        &.with-color {\n            .mmf-select {\n                padding: $mmf-unit 5*$mmf-unit $mmf-unit 3*$mmf-unit;\n            }\n        }\n    }\n}\n","@import \"../../lib/units\";\n\n\n@mixin selectform-solid() {\n    &.mmf-form--select .order--label-first {\n        flex-direction: column;\n        align-items: flex-start;\n\n        .mmf-label {\n            padding-bottom: $mmf-unit;\n        }\n    }\n}\n","@import \"../../lib/colors\";\n@import \"../../lib/units\";\n\n@mixin switch-solid() {\n    &.mmf-switch {\n        outline: none;\n        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n\n        width: 4em;\n        height: 2em;\n        border-radius: .5em;\n        overflow: unset;\n        border: $mmf-input-border--width solid map-get($color-basic, 3);\n        border-radius: $mmf-radius;\n        background-color: map-get($color-basic, 4);\n\n        &::after{\n            @include elevation(1);\n            height: 2em;\n            width: 50%;\n            top: -6%;\n            left: -5%;\n            border: $mmf-input-border--width solid map-get($color-basic, 3);\n            border-radius: $mmf-radius;\n        }\n\n        &:checked {\n            background-color: map-get($color-solid , 2);\n            border-color: map-get($color-solid, 0);\n            &:after {\n                left: 105%;\n                transform: translate(-100%, 0);\n                border-color: map-get($color-solid , 0);\n            }\n        }\n    }\n}\n","@import \"../../lib/input\";\n@import \"../../lib/units\";\n\n\n@mixin textarea-solid() {\n    &.mmf-textarea {\n        @include with-input-solid();\n        padding: $mmf-unit;\n\n        &[disabled] {\n            color: rgba(map-get($color-solid, 7), map-get($opacity, 2));\n        }\n    }\n}\n","@import \"../components/button/button-solid\";\n@import \"../components/buttonform/buttonform-solid\";\n@import \"../components/checkbox/checkbox-solid\";\n@import \"../components/checkboxform/checkboxform-solid\";\n@import \"../components/input/input-solid\";\n@import \"../components/list/list-solid\";\n@import \"../components/popover/popover-solid\";\n@import \"../components/radiobuttons/radiobuttons-solid.scss\";\n@import \"../components/select/select-solid\";\n@import \"../components/selectform/selectform-solid\";\n@import \"../components/switch/switch-solid\";\n@import \"../components/textarea/textarea-solid\";\n@import \"../lib/typography\";\n@import \"../lib/colors\";\n@import \"../lib/units\";\n@import \"../lib/fonts\";\n\n@mixin with-solid-theme() {\n    @include button-solid(