  width: 100%;\n        position: absolute;\n        top: 0;\n        display: inline-block;\n        transition: all $mmf-transition--time ease-in-out;\n\n        color: map-get($color-basic, 7);\n        opacity: map-get($opacity, 1);\n        font-size: $mmf-text-size--label; // small font-size if not currently a placeholder (but title)\n\n        white-space: nowrap;\n        text-overflow: ellipsis;\n        overflow: hidden;\n    }\n\n    &.has-focus {\n        .mmf-label {\n            color: map-get($color-material, 0);\n            opacity: map-get($opacity, 0);\n        }\n    }\n\n    &.has-error {\n        select,\n        input {\n            border-color: map-get($color-danger, 1);\n        }\n        &.has-focus {\n            .mmf-label {\n                color: map-get($color-danger, 1);\n            }\n        }\n    }\n\n    &.has-warning {\n        select,\n        input {\n            border-color: map-get($color-danger, 3);\n        }\n        &.has-focus {\n            .mmf-label {\n                color: map-get($color-danger, 3);\n            }\n        }\n    }\n\n    &.is-empty.no-focus {\n        .mmf-label {\n            transform: translateY($mmf-height-label);\n            font-size: 1em;\n        }\n    }\n}\n","@import \"../lib/with-material-theme\";\n\n\n.the-material {\n    @include with-material-theme();\n}\n","@import \"../../lib/typography\";\n@import \"../../lib/units\";\n@import \"../../lib/reset\";\n@import \"../../lib/colors\";\n@import \"../../lib/interface\";\n@import \"../../lib/elevation\";\n\n\n@mixin button-material() {\n    &.mmf-button {\n        @include reset-button();\n\n        border-radius: $mmf-radius;\n        color: map-get($color-material, 0);\n        background-color: inherit;\n        transition: background-color $mmf-transition;\n\n        &.is-enabled {\n            &:hover,\n            &:focus {\n                background-color: map-get($color-material, 2);\n            }\n        }\n\n        &.is-